.quick-links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    position: relative;

    z-index: 1;

    @include media-breakpoint-up(lg) {
        justify-content: left;

        margin-top: -24px;
    }


    li {
        border: 5px solid get-color(greyscales, white);
    }

    &--homepage {
        @include media-breakpoint-down(sm) {
            display: none;
        }
    }

    &__more {
        position: relative;
        ul {
            display: none;
            position: absolute;
            left: 0;
            top: calc(100% + 5px);

            width: auto;

            padding: 0;

            list-style-type: none;

            li {
                border: none;
                border-top: 5px solid get-color(greyscales, white);

                a {
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
        }

        &:hover {
            ul {
                display: block;
            }
        }
    }

    &--small {
        margin-left: -15px;
        margin-right: -15px;

        justify-content: between;

        li {
            //min-width: 15%;

            a {
                width: 100%;

                text-align: center;
            }


        }

        @include media-breakpoint-down(sm) {
            display: none;
        }

    }
    &--inline {
        margin-top: 0;
        justify-content: start;

        a.button {
            font-size: pxtorem(12px) !important;
        }

        svg {
            display: none;
        }

        li {
            min-width: calc(17% - 30px);
            margin: 15px 15px 0 15px;

            border: none;
        }

        @include media-breakpoint-down(sm) {
            display: flex;
        }

        @include media-breakpoint-down(sm) {
            min-width: calc(25% - 30px);
            display: flex;

            .button {
                background: none;

            }

            li {
                width: 100%;
            }

            svg {
                display: inline-block;
            }
        }


    }
}